/* css reset */
*{box-sizing: border-box;}
*::after, *::after{box-sizing: border-box;} /* 伪元素 */
*{margin: 0; padding: 0;}
ul,ol{list-style: none;}
a{color: inherit;text-decoration: none;}
img{max-width: 100%;max-height: 100%;}

 /* style  */
 body{background: #eee;}

.globalHeader {
    margin: 20px;
}
@media(min-width:500px){
    .globalHeader{
        margin:100px 0 50px;
    }
}
.searchForm{
    display: flex;
    justify-content: space-between;
}
@media (min-width: 500px){
    .searchForm{
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
.searchForm > input {
    width: 100%;
    margin-right: 16px;
    height: 40px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button {
    white-space: nowrap;
    padding: 0 10px;
    border: none;
    width: 80px;
    background: rgb(0, 96, 156);
    border-radius: 4px;
    color : rgb(255, 255, 255) ;
    font-family : Microsoft YaHei ;
    font-size : 16px ;
}

.globalMain {
    max-width: 900px;
    margin-right: auto;
    margin-left: auto;
}

.siteList {
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;   
}
@media(min-width:500px){
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
    }
}
.siteList > li {
    margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList > li {
        margin-right: 25px;
    }
}
.siteList .site {
    width: 160px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    position: relative;
    cursor: pointer;
}
@media(max-width:350px){
    .siteList .site {
        width: 140px;
    }
}
.siteList .site > .close{
    position: absolute;
    right: 5px;
    top: 0;
    cursor: default;
}
@media(min-width:500px){
    .siteList .site > .close{
        display: none;
    }
    .siteList .site:hover > .close{
        display: block;
    }
}
.siteList .site > .logo{
    width: 71px;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size : 64px ;
    text-transform: uppercase;
}
.siteList .site > .link {
    font-family : Microsoft YaHei ;
    font-size : 14px ;
    margin-top: 4px;
}
.siteList .addButton {
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 4px;
    width: 160px;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
    cursor: pointer;
}
@media(max-width:350px){
    .siteList .addButton {
        width: 140px;
    }
}
.siteList .addButton .icon{
    width: 60px;
    height: 60px;
}
.siteList .addButton .icon-wrapper{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}